<template>
  <div class="modal fade" id="couponModal" tabindex="-1" role="dialog"
       aria-labelledby="exampleModalLabel"  ref="modal">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel"></h5>
          <button type="button" class="btn-close"
                  @click="hideModal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="mb-3">
            <label for="title"></label>
            <input type="text" class="form-control" id="title" v-model="tempCoupon.title"
                   placeholder="">
          </div>
          <div class="mb-3">
            <label for="coupon_code"></label>
            <input type="text" class="form-control" id="coupon_code" v-model="tempCoupon.code"
                   placeholder="">
          </div>
          <div class="mb-3">
            <label for="due_date"></label>
            <input type="date" class="form-control" id="due_date"
                   v-model="due_date">
          </div>
          <div class="mb-3">
            <label for="price"></label>
            <input type="number" class="form-control" id="price"
                   v-model.number="tempCoupon.percent" placeholder="">
          </div>
          <div class="mb-3">
            <div class="form-check">
              <input class="form-check-input" type="checkbox"
                     :true-value="1"
                     :false-value="0"
                     v-model="tempCoupon.is_enabled" id="is_enabled">
              <label class="form-check-label" for="is_enabled">
                
              </label>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" @click="hideModal">Close</button>
          <button type="button" class="btn btn-primary"
                  @click="$emit('update-coupon', tempCoupon)">
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import modalMixin from '../mixins/modalMinxin'
import { Modal } from 'bootstrap';
export default {
  name: 'CouponModal',
  props: {
    coupon: {},
  },
  mixins: [modalMixin], 
  data() {
    return {
      tempCoupon: {},
      due_date: '',
    };
  },
  emits: ['update-coupon'],
  mounted() {
    // 
    this.modalInstance = new Modal(this.$refs.modal, {
      backdrop: 'static',  // 
    });
  },
   watch: {
      coupon() {
      this.tempCoupon = this.coupon;
      //  YYYY-MM-DD
      console.log(this.tempCoupon.due_date);
      const dateAndTime = new Date(this.tempCoupon.due_date * 1000)
        .toISOString().split('T');
      [this.due_date] = dateAndTime;
    },
    due_date() {
      this.tempCoupon.due_date = Math.floor(new Date(this.due_date) / 1000);
    },
   },
   methods: {
    showModal() {
      this.modalInstance.show(); //  instance
      
    },
    hideModal() {
       const activeEl = document.activeElement;
        if (this.$refs.modal.contains(activeEl)) {
          activeEl.blur(); //  focus
        }
        this.modalInstance.hide(); //  instance
    },
  },
};
</script>